import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react'
import s from './SuperButton.module.css'

//    , children        
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>,
    HTMLButtonElement>

type SuperButtonPropsType = DefaultButtonPropsType & {
    xType?: string
}

const SuperButton: React.FC<SuperButtonPropsType> = (
    {
        xType,
        className,
        disabled,
        ...restProps //       restProps,    children
    }
) => {
    let finalClassName = s.button
    if (!disabled) {
        if (xType === 'red') {
            finalClassName += ` ${s.red}`;
        } else if (xType === 'secondary') {
            finalClassName += ` ${s.secondary}`;
        } else {
            finalClassName += ` ${s.default}`;
        }
    }
    if (disabled) {
        finalClassName += ` ${s.disabled}`;
    }
    if (className) {
        finalClassName += ` ${className}`;
    }


    //    

    //     finalClassName       :
    // `${s. }  ${xType==='' ?    : xType === 'secondary' ?   :    } ${disabled ?    :   } `
    //       

    return (
        <button
            disabled={disabled}
            className={finalClassName}
            {...restProps} //        (children  )
        />
    )
}

export default SuperButton
